<script>
import Profile from "@/models/profile";
import VueMask from "v-mask";
import Vue from "vue";
export default {
    name: 'Profile',
    data() {
        return {
            currentTab: 1,
            profile: new Profile({}),
            language: 'ru',
            system: true,
            notifications: {
                system: true,
                sms: true,
                email: true,
            }
        }
    },

    methods: {
        updateNotificationSettings() {
            console.log('sdf')
            this.$http.post(API_ROOT + "/api/user/notification-settings", {
                system: this.notifications.system,
                sms: this.notifications.sms,
                email: this.notifications.email,
                language: this.language,
            });
        },
        getImage() {
            // const path = 'https://api.djoon.kz/images' + this.user.avatar
            const path = this.profile.avatar || 'http://localhost:8080/avatars/Avatar-2.svg'
            return `background-image: url('${path}')`;
        },
        saveProfileData() {
            this.currentTab = 0
        }
    },
  beforeCreate() {
    Vue.use(VueMask);
  },
}
</script>

